'use strict';



$(document).ready(function () {
    $('.carousel_how-works').slick({
        infinite: true,
        slidesToScroll: 1,
        autoplay: true,
        autoplaySpeed: 10000000,
        dots: true,
        centerMode: false,
        responsive: [{
            breakpoint: 768,
            settings: {
                slidesToShow: 1
            }
        }]
    });

    $('.carousel-advantages').slick({
        infinite: true,
        slidesToShow: 3,
        slidesToScroll: 1,
        autoplay: true,
        autoplaySpeed: 100000,
        dots: true,
        centerMode: true,
        responsive: [{
            breakpoint: 1001,
            settings: {
                slidesToShow: 1
            }
        }]
    });

    $('.carousel_clients').slick({
        infinite: true,
        slidesToShow: 1,
        slidesToScroll: 1,
        autoplay: true,
        autoplaySpeed: 100000,
        dots: false,
        centerMode: true

    });

    $("a[href^='#']").click(function () {
        var _href = $(this).attr("href");
        $("html, body").animate({ scrollTop: $(_href).offset().top + "px" });
        return false;
    });

    $('.subscription-form').submit(function (e) {
        e.preventDefault(); 

        var $form = $(this);

        $.ajax({
            type: "POST",
            url: "mailer/smart.php",
            data: $form.serialize()
        }).done(function () {
            $form.find("input").val("");
            $form.hide();
            $('.success').show();
            console.log('ok');


            $form.trigger('reset');
        });
    });

    function burgerMenu(selector) {
        var menu = $(selector);
        var button = menu.find('.burger-menu_button', '.burger-menu_lines');
        var links = menu.find('.burger-menu_link');
        var overlay = menu.find('.burger-menu_overlay');

        button.on('click', function (e) {
            e.preventDefault();
            toggleMenu();
        });

        links.on('click', function () {
            return toggleMenu();
        });
        overlay.on('click', function () {
            return toggleMenu();
        });

        function toggleMenu() {
            menu.toggleClass('burger-menu_active');

            if (menu.hasClass('burger-menu_active')) {
                $('body').css('overflow', 'hidden');
            } else {
                $('body').css('overflow', 'visible');
            }
        }
    }

    burgerMenu('.burger-menu');
});